.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 80px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f4fcfe;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: Arial, sans-serif;
  font-weight: 100;
}

.video-container {
  position: relative;
  top: 120px;
  width: 50vw; /* Adjust the width as needed */
  height: auto; /* Maintain aspect ratio */
  transition: transform 0.3s ease-in-out; /* Smooth transition for scaling */
  justify-content: center;
  align-items: center;
}

.video-container video {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.video-container.hovered {
  transform: scale(1.1); /* Scale the container to 110% on hover */
}

.video-container.enlarged {
  transform: scale(1.2); /* Scale the container to 120% when clicked */
  z-index: 1;
}
.overlay-text {
  position: absolute;
  top: 40%;
  left: -22%; /* Center between left boundary and video */
  color: black;
  font-size: 19px;
  padding: 10px;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  z-index: 1;
}
.overlay-text.show {
  opacity: 1;
}

@media (max-width: 1000px) {
  .video-container {
    width: 60vw;
  }
  .overlay-text {
    left: -17%;
    font-size: 2vw; /* Adjust font size for smaller screens */
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
