.NavBar {
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  background: linear-gradient(
    to bottom,
    rgb(244, 252, 254, 0.8),
    rgb(244, 252, 254, 0)
  );
  z-index: 1000;
  padding: 10px;
  opacity: 0;
  animation: fadeIn 1s ease-in 01s forwards; /* Apply fade-in animation with delay */
}
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}
.NavBar ul {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.NavBar li {
  margin: 0 30px;
}

.NavBar a {
  text-decoration: none;
  color: black;
  font-family: Arial, sans-serif;
  font-weight: 300;
  font-size: 20px;
}

.NavBar a:hover {
  text-decoration: underline;
}
.home-icon {
  height: 85px; /* Adjust height as needed */
  width: 85px;
  object-fit: contain;
  margin-right: 300px; /* Add space between image and nav items */
}
